ul, ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

ul.no-bullet {
	list-style: none;
}

ul.home-lists, .frontpage-widget-lists {
	margin: 0;
	padding: 0;
}

i {
	cursor: pointer;
}

i.angle-down {
	transform: rotate(-90deg);
}